import Vue from 'vue'
import VueRouter from 'vue-router'
import main_page from '../views/main_page.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main_page',
    component: main_page
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
