<template>
  <div>
    <v-card
      color="#F9F8F8"
      v-for="(item, index) in custommessage"
      :key="index"
      class="my-7"
      elevation="2"
    >
      <v-card-title>
        <div class="titletxt">{{ item.title }}</div>
      </v-card-title>

      <v-row class="px-10">
        <v-col cols="8">
          <div class="subtitletxt">{{ item.explain_txt }}</div>
          <v-text-field
            solo
            dense
            readonly
            :value="item.message"
          ></v-text-field>
        </v-col>
        <v-col cols="4" class="mt-6">
          <v-btn
            color="#FFDD65"
            elevation="0"
            @click="opendialogEditmessage(item.title, item.key)"
            >แก้ไข</v-btn
          >
        </v-col>
      </v-row>
    </v-card>
    <dialogeditmessage ref="editmessage"></dialogeditmessage>
  </div>
</template>

<script>
import axios from "axios";
import dialogeditmessage from "@/components/dialog_editmessage";
export default {
  components: {
    dialogeditmessage,
  },
  data() {
    return {
      enter_message: "หวัดดีเบล",
      custommessage: [
        {
          title: "เข้าพื้นที่",
          explain_txt: "ข้อความที่ส่งหลังจากผู้ใช้เข้าพื้นที่ของ beacon",
          message: "",
          key: 1,
        },
        {
          title: "ทำ Covid tracking เรียบร้อยแล้ว",
          explain_txt:
            "กรณีที่ผู้ใช้ทำ covid tracking และลงเวลาเข้างานอัตโนัติ",
          message: "",
          key: 2,
        },
        {
          title: "ยังไม่ทำ Covid tracking",
          explain_txt: "กรณีที่ผู้ใช้ยังไม่ทำ covid tracking",
          message: "",
          key: 4,
        },
        {
          title: "ข้อความหลังจบ process ทั้งหมด",
          explain_txt: "ข้อความตอบกลับหลังจากการทำงานของระบบเสร็จสิ้น",
          message: "",
          key: 3,
        },
      ],
    };
  },
  computed: {
    updatemessage() {
      return this.$store.state.update_message 
    }
  },
  watch: {
    updatemessage(newValue) {
      if (newValue === true) {
        this.setReplymessage()
        this.$store.commit("Updatemessage", false)
      }
    }
  },
  methods: {
    opendialogEditmessage(title, message_key) {
      this.$refs.editmessage.openDialog(title, message_key);
    },
    async get_replymessage(messageKey){
      var body = {
        key: messageKey,
      }
      const result = await axios.post(
        "http://203.151.164.229:5007/api/v1/admin/get_replymessage",
        body
      );
      return result
    },
    setReplymessage(){
      this.custommessage.forEach(async item => {
        const replymessage = await this.get_replymessage(item.key)
        item.message = replymessage.data.result[0].message
      });
      console.log(this.custommessage);
    }
  },
  created () {
    this.setReplymessage()
  },
};
</script>

<style lang="scss" scoped>
.titletxt {
  font-weight: bold;
  font-size: 20px;
}
.subtitletxt {
  font-size: 15px;
  color: #919090;
}
</style>
