<template>
  <div>
    <v-dialog v-model="dialog" max-width="450">
      <v-card width="800">
        <v-toolbar dark color="#007580" elevation="0">
          <v-toolbar-title>แก้ไขข้อความ</v-toolbar-title>
          <v-spacer></v-spacer>
        </v-toolbar>
        <v-card tile elevation="0">
          <v-col cols="12" sm="12" md="12">
            <v-textarea
              label="message"
              auto-grow
              outlined
              rows="1"
              row-height="75"
              dense
              v-model="message"
            ></v-textarea>
          </v-col>
          <v-divider class="mt-n6"></v-divider>
          <v-card-actions>
            <v-spacer></v-spacer>
            <v-btn text @click="dialog = false">CLOSE</v-btn>
            <v-btn text color="#007580" @click="editmessage(messageKey)">SAVE</v-btn>
          </v-card-actions>
        </v-card>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import axios from "axios";
export default {
  data() {
    return {
      dialog: false,
      title: "",
      message: "",
      messageKey: null
    };
  },
  methods: {
    openDialog(title, message_key) {
      this.title = title
      this.messageKey = message_key
      this.get_replymessage(message_key)
      this.dialog = true
      
    },
    async editmessage(messageKey) {
      var body = {
        key: messageKey,
        message: this.message,
      }
      const result = await axios.post(
        "http://203.151.164.229:5007/api/v1/admin/custommessage",
        body
      );
      console.log(result);
      this.$store.commit("Updatemessage", true)
      this.message = ""
      this.dialog = false
    },
    async get_replymessage(messageKey){
      var body = {
        key: messageKey,
      }
      const result = await axios.post(
        "http://203.151.164.229:5007/api/v1/admin/get_replymessage",
        body
      );
      this.message = result.data.result[0].message
    },
  },
};
</script>

